
import { BButton } from 'buetify/lib/components';
import { useToast } from 'buetify/lib/composables';
import { defineComponent } from 'vue';
export default defineComponent({
	name: 'button-toast-example',
	components: {
		BButton
	},
	setup() {
		const { open } = useToast();

		function onClick() {
			open({
				message: 'Toast!!!',
				variant: 'is-success'
			});
		}
		return {
			onClick
		};
	}
});
